.movie-item{
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: #Fff;
  box-shadow: 2px 3px 6px 4px rgba(250, 248, 248, 0.6);
  border-radius: 5px;
  margin: 20px;
  overflow: hidden;
 
}

.movie-item  img{
  width: 100%;
  margin: 0;
}
.movie-item  h3{
  margin: 0;
  text-decoration: none;
}
.movie-item  a{
  text-decoration: none;
  color: #222;
}
.movie-item  p{
  margin: 10px;
  
}