.movie-list{
  margin: 0 auto;
  width: 100%;
  display: grid;
  grid-template-columns: repeat(4,minmax(200px,1fr));
}


@media (max-width:1000px) {
  .movie-list{
    grid-template-columns: repeat(2,1fr);
    width: 100%;
  }
}

@media (max-width:600px) {
  .movie-list{
    grid-template-columns: 1fr;
    width: 100%;
  }
}