.about-wrap {
  width: 100vw;
  height: 100vh;
  display: flex;
  color: #fff;
  position: relative;
 
}

.poster-about{
  margin-right: 30px;
  width: 500px;
  height: 500px;
}
.original-title {
  margin: 0;
  color: rgb(96, 99, 97);
}
.vote-average {
  border: 3px solid #fff;
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: green;
}
