

nav{
  width: 20%;
  height: 100%;
  box-sizing: border-box;
  padding: 20px;
  color: #fff;
}
nav > h2{
  position: fixed;
  top: 50px;
  left: 20px;
  text-align: center;
  font-size: 2.4rem;
}

nav  p{
  position: fixed;
  top: 170px;
  left: 20px;
  padding: 10px 15px;
  background-color: rgb(207, 10, 10);
  border-radius: 5px;
  font-weight: bold;
  cursor: pointer;
  color: #fff;
  box-shadow: 2px 3px 12px 4px rgba(242, 240, 240, 0.32);
}
nav > div{
  position: fixed;
  top: 260px;
  left: 20px;
  padding: 10px 15px;
  border-radius: 5px;
  box-shadow: 2px 2px 16px 2px rgba(182, 181, 181, 0.32);
}